
import { useEffect, useState } from '@wordpress/element';
import { produce } from 'immer';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, SelectControl, RangeControl, Button, Dashicon, __experimentalUnitControl as UnitControl, TextControl, __experimentalBoxControl as BoxControl, __experimentalBorderControl as BorderControl, ToggleControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';

// Settings Components
import ProModal from './ProModal';
import { generalStyleTabs, } from './utils/options';
import { Label, BtnGroup, Typography, ColorsControl, BColor } from '../../Components';
import { BControlPro } from '../../Components/Pro';

import General from './Components/Settings/General';
import Popup from './Components/Settings/Popup';
import Style from './Components/Settings/StyleTab';
import usePremiumInEditor from './hooks/usePremiumInEditor';


const Settings = ({ attributes, setAttributes }) => {
	const { cId, lightboxType, button, btnType } = attributes;
	const [proModalOpen, setProModalOpen] = useState(false);

	const { isPremium } = usePremiumInEditor();
	const premiumProps = { isPremium, setOpen: setProModalOpen }

	// button Update
	const updateAttr = (attr, type, val) => {
		const newAttr = produce(attributes[attr], draft => {
			draft[type] = val;
		});
		setAttributes({ [attr]: newAttr });
	}

	useEffect(() => {
		if (btnType === 'circle') {
			const ele = document.querySelector(`#lbbLightBox-${cId} .lbbLightBox a`);
			setAttributes({ btnWidth: "auto", btnHeight: ele?.offsetWidth + "px" });

			if (ele.offsetWidth > 300) {
				setTimeout(() => {
					setAttributes({ btnWidth: "auto", btnHeight: ele?.offsetWidth + "px" });
				}, 10);
			}
		}
	}, [btnType, button]);

	const isBtnType = 'button' === lightboxType;

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel lbbTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <General premiumProps={premiumProps} attributes={attributes} setAttributes={setAttributes} isBtnType={isBtnType} updateAttr={updateAttr} Button={Button} Dashicon={Dashicon} PanelBody={PanelBody} PanelRow={PanelRow} RangeControl={RangeControl} SelectControl={SelectControl} TextControl={TextControl} ToggleControl={ToggleControl} UnitControl={UnitControl} />}

				{'popup' === tab.name && <Popup premiumProps={premiumProps} attributes={attributes} setAttributes={setAttributes} PanelBody={PanelBody} ToggleControl={ToggleControl} BControlPro={BControlPro} NumberControl={NumberControl} SelectControl={SelectControl} updateAttr={updateAttr} />}

				{'style' === tab.name && <Style premiumProps={premiumProps} attributes={attributes} setAttributes={setAttributes} PanelBody={PanelBody} PanelRow={PanelRow} BorderControl={BorderControl} BControlPro={BControlPro} NumberControl={NumberControl} Label={Label} BtnGroup={BtnGroup} BColor={BColor} Typography={Typography} ColorsControl={ColorsControl} SelectControl={SelectControl} UnitControl={UnitControl} BoxControl={BoxControl} updateAttr={updateAttr} produce={produce} isBtnType={isBtnType} />}
			</>}</TabPanel>
		</InspectorControls>
		<ProModal setProModalOpen={setProModalOpen} proModalOpen={proModalOpen} />
	</>;
};
export default Settings;