

import { __ } from '@wordpress/i18n';
import { captionPOpt, imageAnimation, pxUnit, perUnit, btnSopt, btnTypeOpt, aligns, } from '../../utils/options';
const Style = ({ premiumProps, attributes, setAttributes, PanelBody, PanelRow, BorderControl, BControlPro, NumberControl, Label, BtnGroup, BColor, Typography, ColorsControl, SelectControl, UnitControl, BoxControl, updateAttr, produce, isBtnType }) => {

    const { lightboxType, content, img, caption, imgBorder, overlyColor, playerColor, btnStyle, captionTypo, captionColors, btnWidth, btnHeight, btnType, btnColors, btnAlign, btnTypo, btnHovColors, btnPadding, btnBorder, button, btnSpaceBetween, btnRadius } = attributes;
    return <>  {
        'gallery' === lightboxType && <>
            <PanelBody className='bPlPanelBody lbbPanelBody' title={__('Gallery Item', 'lightbox')} initialOpen={false}>
                {/* button border  */}
                <Label>{__('Border', 'lightbox')}</Label>
                <BorderControl className="mb20" value={imgBorder} onChange={(value) => setAttributes({ imgBorder: value })}
                />

                <BControlPro className='mb10' label={__('Border Radius', 'lightbox')} labelPosition='left' value={img?.borderRadius} onChange={(val) => updateAttr('img', 'borderRadius', parseInt(val))} min={1} max={100} Component={NumberControl} {...premiumProps} />

                {/* Hover Effect  */}
                <BControlPro labelPosition="side" className='mt10' label={__('Hover Effect', 'lightbox')} value={img?.animation} options={imageAnimation} onChange={val => updateAttr('img', 'animation', val)} Component={SelectControl} {...premiumProps} />
            </PanelBody>
        </>
    }

        < PanelBody className='bPlPanelBody lbbPanelBody' title={__('Player', 'lightbox')} initialOpen={false} >
            <BControlPro className='' label={__('Controls Color', 'lightbox')} value={playerColor} onChange={val => setAttributes({ playerColor: val })} Component={BColor} {...premiumProps} />
        </PanelBody >

        {(content?.caption && lightboxType === 'gallery') && <PanelBody className='bPlPanelBody lbbPanelBody' title={__('Caption', 'lightbox')} initialOpen={false}>
            {/* Typography  */}
            <BControlPro className='' label={__('Typography:', 'lightbox')} value={captionTypo} isFamily={false} onChange={val => { setAttributes({ captionTypo: val }) }} defaults={{ fontSize: 25 }} produce={produce} Component={Typography} {...premiumProps} />

            {/* color */}
            <BControlPro className='' label={__('Colors', 'lightbox')} value={captionColors} onChange={val => setAttributes({ captionColors: val })} defaults={{ color: '#fff', bg: '#00000000' }} Component={ColorsControl} {...premiumProps} />

            {/* Overly Color */}
            <BControlPro className='' label={__('Overly Color')} value={overlyColor} onChange={val => setAttributes({ overlyColor: val })} Component={BColor} {...premiumProps} />

            {/* Caption style  */}
            <BControlPro labelPosition="side" className='mt10' label={__('Caption Style', 'lightbox')} value={caption?.position} options={captionPOpt} onChange={val => updateAttr('caption', 'position', val)} Component={SelectControl} {...premiumProps} />
        </PanelBody>}

        {
            isBtnType &&
            <PanelBody PanelBody className='bPlPanelBody lbbPanelBody' title={__('Button', 'lightbox')} initialOpen={true}>
                {/* button width height  */}
                <div className="lbbWHControl">
                    {/* Widht  */}
                    <UnitControl label={__('Width', 'lightbox')} className='' value={btnWidth}
                        onChange={(val) => { setAttributes({ btnWidth: val }) }} units={[pxUnit(10), perUnit(100)]} onUnitChange="" isUnitSelectTabbable />
                    {/* Height 	 */}
                    <UnitControl label={__('Height', 'lightbox')} className='' value={btnHeight}
                        onChange={(val) => { setAttributes({ btnHeight: val }) }} units={[pxUnit(10), perUnit(100)]} onUnitChange="" isUnitSelectTabbable />
                </div>

                {/* Button Style  */}
                <PanelRow className='mt20 mb20'>
                    <Label mt='0' mb='0'>{__('Style:', 'lightbox')}</Label>
                    <BtnGroup value={btnStyle}
                        onChange={val => {

                            if ('fill' === val) {
                                setAttributes({
                                    btnStyle: val,
                                    btnColors: { color: '#4527a4', bg: '#000', styles: `color: #4527a4; background: #000` }
                                })
                            }

                            if ('outline' === val) {
                                setAttributes({
                                    btnStyle: val,
                                    btnBorder: { color: btnColors?.color, style: 'solid', width: '1px' },
                                    btnColors: { color: '#4527a4', bg: '#fff0', styles: `color: #4527a4; background: #fff0` }
                                })
                            }
                        }
                        }
                        options={btnSopt} isIcon={false} />
                </PanelRow>

                <SelectControl label={__('Type', 'lightbox')} labelPosition="side" value={btnType} options={btnTypeOpt} onChange={val => {
                    const radius = val === 'circle' ? '50%' : '3px';
                    const pillRadius = val === 'pill' ? '100px' : '3px';
                    const pillTBPadding = val === 'pill' ? '10px' : '8px';
                    const pillLRBPadding = val === 'pill' ? '20px' : '15px';
                    const defaultTBPadding = val === 'default' ? '8px' : '8px';
                    const defaultRBPadding = val === 'default' ? '15px' : '15px';

                    if (val === 'default') {
                        setAttributes({
                            btnType: val,
                            btnWidth: 'auto',
                            btnHeight: 'auto',
                            btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
                            btnPadding: { "top": defaultTBPadding, "right": defaultRBPadding, "bottom": defaultTBPadding, "left": defaultRBPadding }
                        })
                    }

                    if (val === 'circle') {
                        setAttributes({
                            btnType: val,
                            btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
                            btnPadding: { "top": pillTBPadding, "right": pillLRBPadding, "bottom": pillTBPadding, "left": pillLRBPadding }
                        })
                    }

                    if (val === 'fullWidth') {
                        setAttributes({
                            btnType: val,
                            btnWidth: '100%',
                            btnHeight: 'auto',
                            btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
                        })
                    }

                    if (val === 'pill') {
                        setAttributes({
                            btnType: val,
                            btnWidth: 'auto',
                            btnHeight: 'auto',
                            btnRadius: { "top": pillRadius, "right": pillRadius, "bottom": pillRadius, "left": pillRadius },
                            btnPadding: { "top": pillTBPadding, "right": pillLRBPadding, "bottom": pillTBPadding, "left": pillLRBPadding }
                        })
                    }
                }} />

                <PanelRow className='mt20'>
                    <Label mt='0' mb='0'>{__('Align:', 'lightbox')}</Label>
                    <BtnGroup value={btnAlign} onChange={val => setAttributes({ btnAlign: val })} options={aligns} isIcon={true} />
                </PanelRow>

                {/* Typography  */}
                <Typography className='' label={__('Typography:', 'lightbox')} value={btnTypo} isFamily={false} onChange={val => { setAttributes({ btnTypo: val }) }} defaults={{ fontSize: 18 }} produce={produce} />

                {/* color */}
                <ColorsControl className='' label={__('Colors', 'lightbox')} value={btnColors} onChange={val => setAttributes({ btnColors: val })} defaults={{ color: '#4527a4', bg: '#fff' }} />

                {/* Hover color  */}
                <ColorsControl label={__('Hover Colors', 'lightbox')} className="mb15" value={btnHovColors} onChange={val => setAttributes({ btnHovColors: val })} defaults={{ color: '#4527a4', bg: '#fff' }} />

                {/* Padding */}
                <BoxControl label={__("Padding", 'lightbox')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })} resetValues={{ top: "8px", right: "15px", bottom: "8px", left: "15px" }} />

                {/* button border  */}
                <Label>{__('Border', 'lightbox')}</Label>
                <BorderControl className="mb20" value={btnBorder} onChange={(value) => setAttributes({ btnBorder: value })}
                />

                {/* border radius  */}
                <BoxControl label={__('Border Radius', 'lightbox')} values={btnRadius} onChange={val => setAttributes({ btnRadius: val })} resetValues={{
                    top: "3px", right: "3px", bottom: "3px", left: "3px"
                }}
                />

                {/* Icon Space */}
                {button.icon.class &&
                    <UnitControl label={__('Space between icon & text', 'lightbox')} className='' value={btnSpaceBetween} onChange={(val) => { setAttributes({ btnSpaceBetween: val }) }}
                        units={[pxUnit(10)]} />
                }
            </PanelBody>
        }
    </>
}
export default Style;