import { __ } from '@wordpress/i18n';
import { TextControl } from '@wordpress/components';
import { produce } from 'immer';

import { Label, InlineMediaUpload } from '../../../../Components';
import { SelectControlPro } from '../../../../Components/Pro';

import { contentType } from '../../utils/options';

const Item = ({ attributes, setAttributes, arrKey, index, setActiveIndex = false, premiumProps }) => {
    const { lightboxType } = attributes;
    const items = attributes[arrKey];
    const { type, thumbnail, content, caption } = items[index];

    const updateItem = (type, index, val, childType = false) => {
        const newItem = produce(items, draft => {
            if (childType) {
                draft[index][type][childType] = val;
            } else {
                draft[index][type] = val;
            }
        });
        setAttributes({ items: newItem });
        setActiveIndex && setActiveIndex(index);
    }

    return <>
        <SelectControlPro label={__('Content Type', 'lightbox')} labelPosition="side" value={type} options={contentType} onChange={val => {
            const newItems = produce(items, draft => {
                draft[index]['type'] = val;
                draft[index]['content'] = 'content' === val ? '<!-- wp:paragraph --><!-- /wp:paragraph -->' : '';
            });
            setAttributes({ items: newItems });
        }} __nextHasNoMarginBottom proValues={['pdf', 'iframe']}  {...premiumProps} />

        {/* Check than add thumbnail or button  */}
        {'image' !== type && 'gallery' === lightboxType && <>
            <Label>{__('Thumbnail:', 'lightbox')}</Label>
            <InlineMediaUpload value={thumbnail} type={['image']} onChange={val => updateItem('thumbnail', index, val)} placeholder={__('Enter Image URL', 'lightbox')} />
        </>}

        {/* Image  */}
        {'image' === type && <>
            <Label>{__('Image:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['image']} onChange={val => { updateItem('content', index, val) }} placeholder={__('Enter Image URL', 'lightbox')} />
        </>}

        {/* Audio  */}
        {'audio' === type && <>
            <Label>{__('Audio:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['audio']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Audio URL', 'lightbox')} />
        </>}

        {/* Video  */}
        {'video' === type && <>
            <Label>{__('Video:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['video']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Video URL', 'lightbox')} />
        </>}

        {/* pdf  */}
        {'pdf' === type && <>
            <Label>{__('Pdf:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['pdf']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Pdf URL', 'lightbox')} />
        </>}

        {'iframe' === type && <>
            <Label>{__('Iframe:', 'lightbox')}</Label>
            <TextControl value={content} onChange={val => updateItem('content', index, val)} placeholder={__('Iframe Map URL', 'lightbox')} />
            <span>{__('Note:Use only src', 'lightbox')}</span>
        </>}

        {/* caption area  */}
        <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
        <TextControl className="mt10" value={caption} onChange={val => {
            updateItem('caption', index, val)
        }} />
    </>
}
export default Item;