

import { __ } from '@wordpress/i18n';
import { Modal } from '@wordpress/components';

const ProModal = ({ proModalOpen, setProModalOpen }) => {
    return proModalOpen && <Modal className='lbbProModal' title={__('Pro Feature', 'lightbox-block')} onRequestClose={() => setProModalOpen(false)}>

        <div className="proList">
            <h3 className='title'>{__('Explore new features in Pro', 'lightbox-block')}</h3>
            <ul className='list'>
                <li className='feature'>{__('Content type image, audio, video, content, pdf, Iframe', 'lightbox-block')}</li>
                <li className='feature'>{__('Show caption and overly color', 'lightbox-block')}</li>
                <li className='feature'>{__('Popup slide autostart', 'lightbox-block')}</li>
                <li className='feature'>{__('Two types of mouse-wheel -- zoom, slide', 'lightbox-block')}</li>
                <li className='feature'>{__('Four type of slide effect -- Default, Slide, CorssFade, Classic', 'lightbox-block')}</li>
                <li className='feature'>{__('Popup topbar info count show', 'lightbox-block')}</li>
                <li className='feature'>{__('Popup middle topbar show Facebook share,twitter share, download, slideshows, thumbnails and close .', 'lightbox-block')}</li>
                <li className='feature'>{__('Popup thumbnails show', 'lightbox-block')}</li>
                <li className='feature'>{__('Two type of thumbnails -- classic and modern', 'lightbox-block')}</li>
                <li className='feature'>{__('Four type of hover effect -- Default, Zoom In, Zoom Out, Rotate In, Rotate Out, Shine', 'lightbox-block')}</li>
                <li className='feature'>{__('Gallery Item border radius', 'lightbox-block')}</li>
                <li className='feature'>{__('Player controls colors', 'lightbox-block')}</li>
                <li className='feature'>{__('Change modal background color', 'lightbox-block')}</li>
            </ul>
            <h3 className='title'>{__('To unlock those features! Upgrade to Pro', 'lightbox-block')}</h3>
        </div>

        <div className='lbbProFeature'>
            <a target="_blank" className='upgradeBtn' href="https://bplugins.com/products/lightbox-block/#pricing" rel="noreferrer"> {__('Upgrade To PRO', 'lightbox-block')}</a>

        </div>
    </Modal>;
};

export default ProModal;