import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
    const { data: lbbUtils } = useWPOptionQuery('lbbUtils');
    const { data = null, refetch, isLoading = true } = useWPAjax('lbbPipeChecker', { _wpnonce: lbbUtils?.nonce }, true);
    const isPremium = (!isLoading && data?.isPipe) || false;

    useEffect(() => {
        refetch()
    }, [lbbUtils]);

    return { isPremium, isLoading };
};
export default usePremiumInEditor;