import { __ } from '@wordpress/i18n';
import { thumbTypeOpt, transitionOpt, wheelOpt } from '../../utils/options';
import { BColor } from '../../../../Components';

const Popup = ({ premiumProps, attributes, setAttributes, PanelBody, ToggleControl, BControlPro, NumberControl, SelectControl, updateAttr }) => {
    const { lightboxType, popupOptions, popupIconLeft, popupIconMiddle, popupIconRight, slideShow, options, controls, thumb, popupTheme } = attributes;
    return <>
        {/* Slideshows */}
        {lightboxType === 'gallery' && <PanelBody title={__("Options", "lightbox")} className="bPlPanelBody" initialOpen={false} >

            <BControlPro className='mb20' label={__('Theme Color', 'slider')} value={popupTheme?.color} defaultColor="#000" onChange={val => { updateAttr('popupTheme', 'color', val); }} Component={BColor} {...premiumProps} />

            <ToggleControl className='mt20' label={__('Mixed All Content', 'lightbox')} checked={popupOptions?.mixAllData} onChange={(val) => { setAttributes({ popupOptions: { ...popupOptions, mixAllData: val } }); }} />

            <BControlPro className='mt10 mb20' label={__('Autoplay/Autostart', 'lightbox')} checked={slideShow?.playOnStart} onChange={val => { updateAttr('slideShow', 'playOnStart', val); }} Component={ToggleControl} {...premiumProps} />

            {slideShow?.playOnStart && <BControlPro className='mb10' label={__('Autoplay Duration(ms)', 'lightbox')} labelPosition='left' value={slideShow?.timeout} onChange={val => updateAttr('slideShow', 'timeout', parseInt(val))} min={1000} max={10000} Component={NumberControl} {...premiumProps} />}

            <BControlPro labelPosition="side" className='mt10' label={__('Mousewheel`', 'lightbox')} value={options?.wheel} options={wheelOpt} onChange={val => updateAttr('options', 'wheel', val)} Component={SelectControl} {...premiumProps} />

            {lightboxType === 'gallery' && <BControlPro labelPosition="side" className='mt10' label={__('Slide Effect', 'lightbox')} value={options?.transition} options={transitionOpt} onChange={val => updateAttr('options', 'transition', val)} Component={SelectControl} {...premiumProps} />}
        </PanelBody>}

        {/* Pro feature  */}
        <PanelBody title={__('Top Bar', 'lightbox')} className="bPlPanelBody" initialOpen={false}>
            <div className="notice">Only the image gallery will work.</div>
            <PanelBody title={__('Left ', 'lighltbox')} className="bPlPanelBody" initialOpen={false} >
                <BControlPro className='mt10' label={__('Count Info', 'lightbox')} checked={popupIconLeft?.infobar} onChange={val => updateAttr('popupIconLeft', 'infobar', val)} Component={ToggleControl} {...premiumProps} />
            </PanelBody>

            <PanelBody title={__('Middle ', 'lighltbox')} className="bPlPanelBody" initialOpen={false} >
                <BControlPro className='mt10' label={__('Zoom In', 'lightbox')} checked={popupIconMiddle?.zoomIn} onChange={val => updateAttr('popupIconMiddle', 'zoomIn', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Zoom Out', 'lightbox')} checked={popupIconMiddle?.zoomOut} onChange={val => updateAttr('popupIconMiddle', 'zoomOut', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Toggle zoom level', 'lightbox')} checked={popupIconMiddle?.toggle1to1} onChange={val => updateAttr('popupIconMiddle', 'toggle1to1', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Rotate Content Clock Wish', 'lightbox')} checked={popupIconMiddle?.rotateCCW} onChange={(val) => { updateAttr('popupIconMiddle', 'rotateCCW', val) }} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Rotate Clock Wish', 'lightbox')} checked={popupIconMiddle?.rotateCW} onChange={val => updateAttr('popupIconMiddle', 'rotateCW', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Flip Horizontally', 'lightbox')} checked={popupIconMiddle?.flipX} onChange={val => updateAttr('popupIconMiddle', 'flipX', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Flip Vertically', 'lightbox')} checked={popupIconMiddle?.flipY} onChange={val => updateAttr('popupIconMiddle', 'flipY', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Fullscreen Mode', 'lightbox')} checked={popupIconMiddle?.fullscreen} onChange={val => updateAttr('popupIconMiddle', 'fullscreen', val)} Component={ToggleControl} {...premiumProps} />
            </PanelBody>

            <PanelBody title={__('Right ', 'lightbox')} className="bPlPanelBody" initialOpen={false}>

                <BControlPro className='mt10' label={__('Facebook Share', 'lightbox')} checked={popupIconRight?.facebook} onChange={val => updateAttr('popupIconRight', 'facebook', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Twitter Share', 'lightbox')} checked={popupIconRight?.twitter} onChange={val => updateAttr('popupIconRight', 'twitter', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Download', 'lightbox')} checked={popupIconRight?.download} onChange={val => updateAttr('popupIconRight', 'download', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('SlideShows', 'lightbox')} checked={popupIconRight?.slideshow} onChange={val => updateAttr('popupIconRight', 'slideshow', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Thumbnails', 'lightbox')} checked={popupIconRight?.thumbs} onChange={val => updateAttr('popupIconRight', 'thumbs', val)} Component={ToggleControl} {...premiumProps} />

                <BControlPro className='mt10' label={__('Close', 'lightbox')} checked={popupIconRight?.close} onChange={val => updateAttr('popupIconRight', 'close', val)} Component={ToggleControl} {...premiumProps} />


            </PanelBody>
        </PanelBody>

        {/* Thumbnails  */}
        {
            lightboxType === "gallery" && <PanelBody title={__("Thumbnails", "lightbox")} className="bPlPanelBody" initialOpen={false} >
                <BControlPro className='mt10' label={__('Show Thumbnails', 'lightbox')} checked={thumb?.showOnStart} onChange={val => updateAttr('thumb', 'showOnStart', val)} Component={ToggleControl} {...premiumProps} />

                {thumb.showOnStart && <BControlPro labelPosition="side" className='mt10' label={__('Type', 'lightbox')} value={thumb?.type} options={thumbTypeOpt} onChange={val => updateAttr('thumb', 'type', val)} Component={SelectControl} {...premiumProps} />}
            </PanelBody>
        }

        <PanelBody title={__("Player Controls", "lightbox")} className="bPlPanelBody" initialOpen={false}>

            <BControlPro className='mt10' label={__('Play large', 'lightbox')} checked={controls["play-large"]} onChange={val => { updateAttr('controls', 'play-large', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Restart', 'lightbox')} checked={controls?.restart} onChange={val => { updateAttr('controls', 'restart', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Rewind', 'lightbox')} checked={controls?.rewind} onChange={val => { updateAttr('controls', 'rewind', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Play', 'lightbox')} checked={controls?.play} onChange={val => { updateAttr('controls', 'play', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Fast-forward', 'lightbox')} checked={controls["fast-forward"]} onChange={val => { updateAttr('controls', 'fast-forward', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Progress', 'lightbox')} checked={controls?.progress} onChange={val => { updateAttr('controls', 'progress', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Current Time', 'lightbox')} checked={controls["current-time"]} onChange={val => { updateAttr('controls', 'current-time', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('duration', 'lightbox')} checked={controls?.duration} onChange={val => { updateAttr('controls', 'duration', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Mute', 'lightbox')} checked={controls?.mute} onChange={val => { updateAttr('controls', 'mute', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Volume', 'lightbox')} checked={controls?.volume} onChange={val => { updateAttr('controls', 'volume', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Pip', 'lightbox')} checked={controls?.pip} onChange={val => { updateAttr('controls', 'pip', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Airplay', 'lightbox')} checked={controls?.airplay} onChange={val => { updateAttr('controls', 'airplay', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Settings', 'lightbox')} checked={controls?.settings} onChange={val => { updateAttr('controls', 'settings', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Download', 'lightbox')} checked={controls?.download} onChange={val => { updateAttr('controls', 'download', val); }} Component={ToggleControl} {...premiumProps} />

            <BControlPro className='mt10' label={__('Full Screen', 'lightbox')} checked={controls?.fullscreen} onChange={val => { updateAttr('controls', 'fullscreen', val); }} Component={ToggleControl} {...premiumProps} />
        </PanelBody>
    </>
}
export default Popup;